body {
  background-color: #121212;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

div {
  background-color: #1f1f1f;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.7);
  width: 300px;
}

label {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
  font-size: 1.1em;
}

input {
  background-color: #333;
  color: #fff;
  border: none;
  padding: 10px;
  border-radius: 5px;
  margin-top: 5px;
}

button {
  background-color: #333;
  color: #fff;
  border: none;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #444;
}

p {
  margin-top: 20px;
  font-size: 1.3em;
  font-weight: bold;
  text-align: left;
  color: #66ff00; /* Выделяем количество дней светлым цветом */
}
